*,
*:before,
*:after {
    box-sizing: border-box;
}
* {
    margin: 0;
    padding: 0;
    font: inherit;
}

@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&family=Fleur+De+Leah&family=Great+Vibes&family=Itim&family=Nabla&display=swap");
.title-h1 {
    font-family: "Fleur De Leah", cursive;
    font-size: 60px;
    color: white;
}

.title-top {
    margin: 20px 0;
}

.title-top h1 {
    font-family: "Fleur De Leah", cursive;
    font-size: 50px;
}

.box-bg2 {
    width: 555px;
    height: 555px;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title-left {
    font-family: "Great Vibes", cursive;
    font-size: 50px;
    margin-top: 10px;
}

.box-left {
    position: relative;
    font-size: 16px;
    font-size: 1.06667rem;
    font-weight: 300;
    color: #797979;
}
.box-left button {
    background-color: #c89d9c;
    color: #fff;
    padding: 15px 10px 15px 10px;
    font-weight: 600;
    border: 0;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    -o-border-radius: 0;
    -ms-border-radius: 0;

    display: inline-block;
    position: relative;
    border-radius: 8px;
    margin: 28px 0;
    font-family: "Itim", cursive;
}

.nameUser {
    font-size: 1.5rem;
    font-weight: 600;
    margin: 2em 0 1em;
}

.wish {
    text-align: center;
    font-size: 1.3rem;
    margin-bottom: 2em;
    width: 75%;
    font-family: "Itim", cursive;
    margin-top: 1em;
}

th,
td {
    padding: 10px;
    z-index: 20;
}

.today {
    position: relative;
}

.today:before {
    position: absolute;
    top: 5px;
    left: 28px;
    content: "12";
    display: inline-block;
    width: 30px;
    height: 30px;
    line-height: 20px;
    padding: 5px;
    background-color: rgb(200, 157, 156);
    color: #fff;
    border-radius: 50%;
    z-index: 1;
}

.time-event {
    display: flex;
    flex-direction: column;
    height: 1054px;
    background-color: #f2eded;
    margin-bottom: 50px;
}

.box-event-detail {
    background-image: url("./image/bg2.png");
    width: 273px;
    height: 579px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.box-event-detail img {
    width: 233px;
    height: 323px;
    background-size: contain;
    border-radius: 8px;
}

.title-event {
    font-size: 1.36667rem;
    position: relative;
    margin: 1.4em 0 0.5em;
    padding: 0 0 0.8em;
    font-family: inherit;
    font-weight: 500;
    line-height: 1.1;
}

.title-event::after {
    position: absolute;
    bottom: 0;
    left: 0;
    content: "";
    width: 80%;
    border-bottom: 3px solid gray;
    display: flex;
    margin-left: 10%;
}

.address {
    font-family: "Itim", cursive;
}

.box-videos {
    width: 100%;
    height: 496px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 20px 0;
    background-image: url("./image/DSC_2410.JPG");
    background-size: cover;
    background-position: center;
}
.box-videos h1 {
    font-family: "Dancing Script", cursive;
    font-size: 60px;
    text-align: center;
    font-weight: 500;
    line-height: 66px;
    color: #fff;
}
.box-videos button {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    color: #af7372;
    border: 2px solid rgba(231, 76, 60, 1);
    background-color: rgba(200, 157, 156, 0.5);
}
.box-videos p {
    color: #ffffff;
}

.title-table-date th {
    border-bottom: 1px solid #c89d9c;
    font-family: "Itim", cursive;
}

.rain-drop {
    background-image: url("./image/heart.png");
    background-repeat: no-repeat;
    animation: rainFall 2s linear 2s infinite;
    z-index: 999999;
    width: 33.99px;
    height: 33.99px;
    font-size: 0;
}
.layer-1 .rain-drop {
    position: absolute;
    top: 0;
    height: 100vh;
}

.rain-drop:nth-child(2) {
    left: 10%;
}

@keyframes rainFall {
    0% {
        top: 0px;
        left: 10px;
        transform: scale(0.1, 0.1);
    }
    10% {
        top: 5px;
        left: -10px;
        transform: scale(0.1, 0.1);
    }

    20% {
        top: 10px;
        left: -10px;
        transform: scale(0.1, 0.1);
    }

    30% {
        top: 15px;
        left: -10px;
        transform: scale(0.1, 0.1);
    }

    40% {
        top: 20px;
        left: -10px;
        transform: scale(0.1, 0.1);
    }
    50% {
        top: 25px;
        left: 10px;
        transform: scale(0.5, 0.5);
    }
    60% {
        top: 30px;
        left: -10px;
        transform: scale(0.1, 0.1);
    }
    70% {
        top: 35px;
        left: -10px;
        transform: scale(0.1, 0.1);
    }
    80% {
        top: 40px;
        left: -10px;
        transform: scale(0.1, 0.1);
    }
    95% {
        top: 45px;
        left: -10px;
        transform: scale(0.5, 0.5);
    }
    100% {
        left: 10px;
        top: 50px;
        transform: scale(0.5, 0.1);
    }
}

