    }
}

@media only screen and (max-width: 375px) {
    .title-top h1 {
        font-size: 13px;
    }
}

@media only screen and (min-width: 480px) {
    .time-event-detail {
        width: 100%;
        height: 609px;
        position: absolute;
        top: 50%;
        transform: translate("-50% , -50%") !important;
        display: flex;
        justify-content: center;
        align-items: center;
        column-gap: 20px;
        flex-wrap: wrap;
    }

    .bg2 {
        background-image: url("./image/bg2.png");
        width: 100%;
        height: 725px;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 20px;
    }

    .bg-event {
        height: 632px;
        background-image: url("./image/DSC_2363.JPG");
        background-size: cover;
        background-position: center;
        position: relative;
    }

    .slider-images {
        margin: 0px 10px;
    }
    .box-Show-Video {
        padding: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 830px;
        height: 630px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        z-index: 9999999;
        background: "#ffffff";
        border-radius: 8px;
        animation: 0.3s ease-in slide-from-bottom;
        transition: all;
        background-color: #ffffff;
    }
}

.overlays {
    background: rgba(0, 0, 0, 0.9);
    width: 100%;
    z-index: 9999;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    color: red;
}

@keyframes slide-from-bottom {
    0% {
        transform: translateY(100%);
    }

    100% {
        transform: translateY(0);
    }
}
